import React from 'react'
import { Link } from 'gatsby'
import 'animate.css/animate.min.css'
import ScrollAnimation from 'react-animate-on-scroll'
import styled, { css } from 'styled-components'
import theme from '@style/theme'

class Navi extends React.Component {
  render() {
    const { location, title } = this.props
    return (
      <Nav>
        <ScrollAnimation
          animateIn="fadeIn"
          animateOut="fadeOut"
          offset={0}
          duration={0.3}
        >
          <Link to="/">
            <Brand>{title}</Brand>
          </Link>
        </ScrollAnimation>
        <ScrollAnimation
          animateIn="fadeIn"
          animateOut="fadeOut"
          offset={0}
          duration={0.3}
        >
          {location.pathname === '/about/' ? (
            <Link to="/about/">
              <NavLink focus>About</NavLink>
            </Link>
          ):(
            <Link to="/about/">
              <NavLink normal>About</NavLink>
            </Link>
          )}
        </ScrollAnimation>
        <ScrollAnimation
          animateIn="fadeIn"
          animateOut="fadeOut"
          offset={0}
          duration={0.3}
        >
        {location.pathname === '/how/' ? (
            <Link to="/how/">
              <NavLink focus>How</NavLink>
            </Link>
          ):(
            <Link to="/how/">
              <NavLink normal>How</NavLink>
            </Link>
        )}
          </ScrollAnimation>
      </Nav>
    )
  }
}

export default Navi

const Nav = styled.div`
  position: fixed;
  font-family: ${theme.font.primary};
  width: 100%;
  padding: 5px 0;
  @media (max-width: ${theme.screen.md}) {
    display: grid;
    grid-template-columns: repeat(auto-fill, 100px);
  }
`
const Brand = styled.div`
  padding: 10px 20px;
  font-weight: bold;
  ${theme.fontSize.lg};
  @media (max-width: ${theme.screen.md}) {
    padding: 10px 15px;
  }
`
const NavLink = styled.div`
  text-decoration: none;
  float: left;
  display: block;
  padding: 0px 20px;
  font-weight: bold;
  width: 100%;
  ${theme.fontSize.sm};
  color: ${theme.color.grayscale.S950};
  @media (max-width: ${theme.screen.md}) {
    padding: 14px 15px;
    display: inline-block;
  }
  ${props =>
    props.normal &&
    css`
      :before {
        content: '';
        padding-left: 0;
        overflow: hidden;
        transition: 0.2s padding;
      }
      :hover{
        color: ${theme.color.grayscale.S950};
        :before{
          padding: 0 3px;
        }
      }
    `
  }
  ${props =>
    props.focus &&
    css`
      color: ${theme.color.primary.S400};
      :before {
        content: '⁃';
        margin-right: 5px;
      }
    `
  }
`